// import * as ymaps from "yandex-maps";

declare let ymaps: any;

export const contactMapInit = () => {
  const init = () => {
    const contactMap = new ymaps.Map("map", {
      center: [55.856730, 37.600515],
      zoom: 17,
      controls: []
    });
    contactMap.behaviors.disable('scrollZoom');
    contactMap.geoObjects.add(
      new ymaps.Placemark([55.856730, 37.600515], {}, {
        iconLayout: 'default#image',
        iconImageHref: 'data:image;base64,',
        iconImageSize: [40, 40]
      })
    )
  }

  docReady(ymaps.ready().then(init));
}

const docReady = (fn) => {
  if (document.readyState === "complete" || document.readyState === "interactive") {
    setTimeout(fn, 1);
  } else {
    document.addEventListener("DOMContentLoaded", fn);
  }
}
