import SwiperCore, { Autoplay, Swiper } from 'swiper/core';

SwiperCore.use([Autoplay]);

export const techSlider = () => {
  new Swiper('#tech-slider', {
    slidesPerView: 7,
    spaceBetween: 30,
    centeredSlides: true,
    loop: true,
    on: {
      progress: (swiper, progress) => {
        swiper.wrapperEl.style.perspectiveOrigin = (progress * 350 + 75) + '%';
      }
    },
    autoplay: {
      delay: 2500,
      disableOnInteraction: false,
    },
    speed: 600
  });
}
